.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0 20px 0;
}

.header {
  font-family: Montserrat;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 29px;
  color: #333333;
}

.number-of-stays {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 17px;
  color: #4f4f4f;
}

.imageContainer {
  width: 100%;
  height: 269px;
  overflow: hidden;
  border-radius: 24px;
  background-color: #e4e4e4;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 600ms ease;
}

.image:hover {
  transform: scale(1.05);
}

.apartment-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0 10px 0;
}

.apartment-details {
  display: flex;
  align-items: center;
}

.apartment-type {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 17px;
  color: #828282;
}

.apartment-class {
  width: 100px;
  height: 28px;
  border: 1px solid #4f4f4f;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.apartment-class > p {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #4f4f4f;
}

.rating-container {
  display: flex;
  align-items: center;
}

.rating {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 17px;
  color: #4f4f4f;
}

.title {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 1rem;
  line-height: 20px;
  color: #333333;
}

.apartments-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 30px;
  grid-row-gap: 50px;
}
